.Pokecard{
    text-align: center;
    width:300px;
    padding:10px;
    margin:1rem;
    box-shadow: 7px 10px 12px -5px rgba(0,0,0,0.56);
    background-color: white;
    border-radius: 3px;
}
.Pokecard-data{
    font-size:80%;
}

.Pokecard-img{
    display: flex;
    justify-content: center;
    align-items: center;
    height:250px;
    background-color: #eceff1;
    border-radius: 3px;
    
}

.Pokecard img:hover{
    transform: scale(1.25);
    transition: all ease 500ms;
    opacity: 0.8;
}

.Pokecard-title{
    margin-bottom: 1rem;
    font-weight: bold;
    font-size: 1.5rem;
}